export function getCfg (apath, upath) {
  let pouchpath = path.resolve(upath, 'pouch')
  fse.ensureDirSync(pouchpath)
  let dnames = fse.readdirSync(pouchpath)
  log('___A-CFG dnames', dnames, ':end')
  if (!dnames.length) return installDBs(apath, upath)
  else return checkCfg(apath, upath, dnames)

}

function checkCfg(apath, upath, dnames) {
  log('___________CHECK CFG')
  let pouchpath = path.resolve(upath, 'pouch')
  return Promise.all(dnames.map(function(dname) {
    let dbpath = [pouchpath, dname].join('/')
    let remoteDB = new PouchDB(dbpath)
    return remoteDB.get('description')
      .then(descr=> {
        descr.dname = dname
        return descr
      })
      .catch(err=> {
        if (err.reason == 'missing') return
        log('CFG-ERR:', err.reason)
      })
  }))
    .then(descrs=> {
      let cfg = _.compact(descrs)
      // cfg.forEach(dict=> { dict.active = true })
      // let active = _.filter(cfg, dict=> { return dict.active })
      // let dnames = active.map(dict=> { return dict.dname })
      // log('_____________________A:2:dnames', dnames)
      // почему-то здесь базы не определяются, приходится вызывать initDBs
      // setDBs(upath, dnames)
      // log('_____________________A:3: db_terms', db_terms.name)
      // log('_____________________A:4: dbs', dbs.length)
      return cfg
    })
}

export function setDBs (upath, dnames) {
  dbs = []
  dnames.forEach((dn, idx) => {
    if (dn == 'flex') return
    if (dn == 'terms') return
    let dpath = path.resolve(upath, 'pouch', dn)
    let pouch = new PouchDB(dpath)
    pouch.dname = dn
    pouch.weight = idx
    dbs.push(pouch)
  })

  let flexpath = path.resolve(upath, 'pouch', 'flex')
  db_flex = new PouchDB(flexpath)
  let termpath = path.resolve(upath, 'pouch', 'terms')
  db_terms = new PouchDB(termpath)
  log('________DB TERMS', termpath)
  log('________DB FLEX', flexpath)
  log('________DBS', dbs.length)
}

